
.projects-container {
    min-height: 100vh;
    color: rgba(33, 28, 28, 0.85);
    padding: 10rem;
    margin-block: 2rem;
    @media(max-width: 1100px) {
        padding: 2rem;
    }

    &.night {
        .big-header {
            color: rgb(238, 237, 237);
        }
        color: #dadee4;
        .projects-body {
            background-color: transparent;

        }
    }

    .projects-header {
        margin-block-end: 4rem;
    }

    .project-skills {
        padding: 1rem;
        display: flex;
        align-items: center;

        .skill-list {
            gap: 5px;
        }
    }

    .projects-body {
        display: flex;
        flex-direction: column;
        border-radius: 25px;
        padding: 2rem;

        @media(max-width: 1100px) {
            border: none;
            outline: none;
            background-color: transparent;
        }
    }
    .hide {
        opacity: 0;
        transition: opacity .6 ease;
      }
}