.header-container {
  position: sticky;
  top: 0;
  background-color: #ffffff;
  min-height: 150px;
  // box-shadow: 0 5px 50px 10px rgba(255, 255, 255, 0.349);
  z-index: 10;
  color: #201d21;
  transition: top .75s ease;

  .logo {
    background-size: contain;
    height: 250px;
    background-position: center;
    background-image: url('../../img/logo-day.png');
    background-repeat: no-repeat;
    &:hover {
      background-image: url('../../img/logo-day-moving.gif');
    }
    @media (max-width: 600px) {
      height: 175px;
    }
  }

  &.night  {
    background-color: #1e1919;
    // box-shadow: 0 5px 50px 10px rgba(45, 44, 44, 0.337);
    color: rgb(220, 217, 217);

    .logo {
      background-image: url('../../img/logo-night.png');
      &:hover {
        background-image: url('../../img/logo-night-moving.gif');
      }
    }
  }
  
  &.hide {
    top: -290px;
    transition: top .75s ease;
  }

  .header-logo {
    font-family: 'Satisfy', cursive;
    font-size: 3rem;
    user-select: none;
    width: 100%;
    align-self: center;
    span {
      font-weight: 800;
      flex-grow: 1;
      text-align: center;
    }

    .switch-wrapper {
      flex-basis: 50px;
    }

  }
  nav {
    gap: 20px;
    align-items: center;
    font-family: 'Lato', sans-serif;
    user-select: none;
    flex: 1;

    ul {
      gap: 15px;
    }
    .nav-link {
      transition: 0.3s all;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      position: relative;
      padding-block-end: 3px;
      font-size: 1.5rem;
      @media (max-width: 600px) {
        padding-block-end: 0;
        margin-block-end: 1rem;
      }

      &.active {
        color: #6c2183;
        
      }
    }

    li {
      gap: 1.5px;
      &:not(.active):hover .dot {
        transition: all .5s ease-in-out;
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.transparent {
    background-color: transparent;
    transition: all .75s ease;
  }

}

